"use client";

import { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import Image from "next/image";
import Link from "next/link";
import API, { getImageUrl, BACKEND_URL } from "@/lib/api";
import { Order, Bill } from "@/types";
import toast from "react-hot-toast";
import {
  FiArrowLeft,
  FiClock,
  FiPackage,
  FiCheckCircle,
  FiXCircle,
  FiFileText,
  FiDownload,
  FiCreditCard,
} from "react-icons/fi";
import { formatCurrency } from "@/lib/currency";

const STATUS_COLORS: Record<string, { bg: string; text: string; dot: string }> = {
  pending: { bg: "bg-yellow-50", text: "text-yellow-700", dot: "bg-yellow-500" },
  confirmed: { bg: "bg-blue-50", text: "text-blue-700", dot: "bg-blue-500" },
  preparing: { bg: "bg-orange-50", text: "text-orange-700", dot: "bg-orange-500" },
  served: { bg: "bg-purple-50", text: "text-purple-700", dot: "bg-purple-500" },
  completed: { bg: "bg-green-50", text: "text-green-700", dot: "bg-green-500" },
  cancelled: { bg: "bg-red-50", text: "text-red-700", dot: "bg-red-500" },
};

const PAYMENT_METHODS = [
  { value: "cash", label: "Cash" },
  { value: "esewa", label: "eSewa" },
  { value: "khalti", label: "Khalti" },
  { value: "bank", label: "Bank Transfer" },
] as const;

export default function OrderDetailPage() {
  const params = useParams();
  const orderId = params.id as string;

  const [order, setOrder] = useState<Order | null>(null);
  const [bill, setBill] = useState<Bill | null>(null);
  const [loading, setLoading] = useState(true);
  const [generatingBill, setGeneratingBill] = useState(false);
  const [payingBill, setPayingBill] = useState(false);
  const [selectedPayment, setSelectedPayment] = useState<"cash" | "esewa" | "khalti" | "bank">("cash");

  useEffect(() => {
    if (orderId) {
      fetchOrder();
    }
  }, [orderId]);

  const fetchOrder = async () => {
    setLoading(true);
    try {
      const res = await API.get(`/orders/${orderId}`);
      const fetchedOrder = res.data.order || res.data;
      setOrder(fetchedOrder);
      await checkBill();
    } catch (error: any) {
      toast.error(error.response?.data?.message || "Failed to load order");
    } finally {
      setLoading(false);
    }
  };

  const checkBill = async () => {
    try {
      const res = await API.get(`/bills?order=${orderId}`);
      const bills = res.data.bills || res.data || [];
      if (Array.isArray(bills) && bills.length > 0) {
        setBill(bills[0]);
      } else if (res.data.bill) {
        setBill(res.data.bill);
      }
    } catch {
      // No bill exists yet, that's fine
    }
  };

  const requestBillWithOptions = async (options: { callWaiter?: boolean } = {}) => {
    setGeneratingBill(true);
    try {
      const res = await API.post(`/bills/${orderId}/request`, {
        callWaiter: options.callWaiter || false,
      });
      const newBill = res.data.bill || res.data;
      setBill(newBill);
      toast.success(
        options.callWaiter
          ? "Waiter has been called for your bill!"
          : "Bill requested successfully!"
      );
    } catch (error: any) {
      toast.error(error.response?.data?.message || "Failed to request bill");
    } finally {
      setGeneratingBill(false);
    }
  };

  const downloadPdf = () => {
    if (!bill) return;
    const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;
    const baseUrl = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5000/api";
    const url = `${baseUrl}/bills/${bill._id}/pdf`;
    window.open(`${url}?token=${token}`, "_blank");
  };

  const payBill = async () => {
    if (!bill) return;
    setPayingBill(true);
    try {
      const res = await API.put(`/bills/${bill._id}/pay`, {
        paymentMethod: selectedPayment,
      });
      const updatedBill = res.data.bill || res.data;
      setBill(updatedBill);
      // Also refresh order to get updated isPaid
      const orderRes = await API.get(`/orders/${orderId}`);
      setOrder(orderRes.data.order || orderRes.data);
      toast.success("Payment successful!");
    } catch (error: any) {
      toast.error(error.response?.data?.message || "Payment failed");
    } finally {
      setPayingBill(false);
    }
  };

  const formatDate = (dateStr: string) => {
    return new Date(dateStr).toLocaleDateString("en-IN", {
      day: "numeric",
      month: "short",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  const canShowBillActions =
    order &&
    (order.status === "completed" || order.status === "served");

  if (loading) {
    return (
      <div className="space-y-6">
        <div className="h-5 w-32 rounded bg-gray-200 animate-pulse" />
        <div className="rounded-xl bg-white p-6 shadow-sm animate-pulse">
          <div className="space-y-3">
            <div className="h-6 w-48 rounded bg-gray-200" />
            <div className="h-4 w-64 rounded bg-gray-200" />
            <div className="flex gap-2">
              <div className="h-6 w-20 rounded-full bg-gray-200" />
              <div className="h-6 w-20 rounded-full bg-gray-200" />
            </div>
          </div>
        </div>
        <div className="rounded-xl bg-white p-6 shadow-sm animate-pulse">
          <div className="h-5 w-24 rounded bg-gray-200 mb-4" />
          {Array.from({ length: 3 }).map((_, i) => (
            <div key={i} className="flex gap-4 py-4 border-b border-gray-50 last:border-0">
              <div className="h-16 w-16 shrink-0 rounded-lg bg-gray-200" />
              <div className="flex-1 space-y-2">
                <div className="h-4 w-3/4 rounded bg-gray-200" />
                <div className="h-3 w-1/4 rounded bg-gray-200" />
              </div>
              <div className="h-4 w-16 rounded bg-gray-200" />
            </div>
          ))}
        </div>
        <div className="rounded-xl bg-white p-6 shadow-sm animate-pulse">
          <div className="space-y-3">
            <div className="h-4 w-full rounded bg-gray-200" />
            <div className="h-4 w-full rounded bg-gray-200" />
            <div className="h-5 w-full rounded bg-gray-200" />
          </div>
        </div>
      </div>
    );
  }

  if (!order) {
    return (
      <div className="space-y-6">
        <Link
          href="/orders"
          className="inline-flex items-center gap-2 text-sm font-medium text-[#c47a5a] transition-colors hover:underline"
        >
          <FiArrowLeft className="h-4 w-4" />
          Back to Orders
        </Link>
        <div className="rounded-xl bg-white py-20 text-center shadow-sm">
          <FiPackage className="mx-auto mb-4 h-16 w-16 text-gray-300" />
          <p className="text-lg font-medium text-gray-500">Order not found</p>
        </div>
      </div>
    );
  }

  const colors = STATUS_COLORS[order.status];

  return (
    <div className="space-y-6">
      {/* Back Link */}
      <Link
        href="/orders"
        className="inline-flex items-center gap-2 text-sm font-medium text-[#c47a5a] transition-colors hover:underline"
      >
        <FiArrowLeft className="h-4 w-4" />
        Back to Orders
      </Link>

      {/* Order Header */}
      <div className="rounded-xl bg-white p-6 shadow-sm">
        <div className="flex flex-wrap items-start justify-between gap-4">
          <div className="space-y-1.5">
            <h1 className="text-xl font-bold text-gray-800">
              Order #{order._id.slice(-6).toUpperCase()}
            </h1>
            <div className="flex flex-wrap items-center gap-3 text-sm text-gray-500">
              <span className="flex items-center gap-1.5">
                <FiClock className="h-4 w-4" />
                {formatDate(order.createdAt)}
              </span>
              {order.tableNumber && (
                <span className="flex items-center gap-1.5">
                  <FiPackage className="h-4 w-4" />
                  Table {order.tableNumber}
                </span>
              )}
            </div>
          </div>

          <div className="flex flex-wrap items-center gap-2">
            <span
              className={`inline-flex items-center gap-1.5 rounded-full px-3 py-1 text-sm font-medium ${colors.bg} ${colors.text}`}
            >
              <span className={`h-2 w-2 rounded-full ${colors.dot}`} />
              {order.status.charAt(0).toUpperCase() + order.status.slice(1)}
            </span>
            <span
              className={`inline-flex items-center gap-1.5 rounded-full px-3 py-1 text-sm font-medium ${
                order.isPaid
                  ? "bg-green-50 text-green-700"
                  : "bg-gray-100 text-gray-500"
              }`}
            >
              {order.isPaid ? (
                <FiCheckCircle className="h-3.5 w-3.5" />
              ) : (
                <FiXCircle className="h-3.5 w-3.5" />
              )}
              {order.isPaid ? "Paid" : "Unpaid"}
            </span>
          </div>
        </div>
      </div>

      {/* Order Items */}
      <div className="rounded-xl bg-white p-6 shadow-sm">
        <h2 className="mb-4 text-lg font-semibold text-gray-800">
          Items ({order.items.length})
        </h2>
        <div className="divide-y divide-gray-100">
          {order.items.map((item, index) => (
            <div key={index} className="flex items-center gap-4 py-4 first:pt-0 last:pb-0">
              <Image
                src={getImageUrl(item.image)}
                alt={item.name}
                width={64}
                height={64}
                unoptimized
                className="h-16 w-16 shrink-0 rounded-lg object-cover"
              />
              <div className="min-w-0 flex-1">
                <p className="text-sm font-semibold text-gray-800">{item.name}</p>
                <p className="mt-0.5 text-xs text-gray-500">
                  ₹{item.price.toFixed(2)} x {item.quantity}
                </p>
              </div>
              <span className="shrink-0 text-sm font-semibold text-gray-800">
                ₹{(item.price * item.quantity).toFixed(2)}
              </span>
            </div>
          ))}
        </div>
      </div>

      {/* Notes */}
      {order.notes && (
        <div className="rounded-xl bg-white p-6 shadow-sm">
          <h2 className="mb-2 text-lg font-semibold text-gray-800">Notes</h2>
          <p className="text-sm text-gray-600">{order.notes}</p>
        </div>
      )}

      {/* Order Summary */}
      <div className="rounded-xl bg-white p-6 shadow-sm">
        <h2 className="mb-4 text-lg font-semibold text-gray-800">
          Order Summary
        </h2>
        <div className="space-y-3">
          <div className="flex items-center justify-between text-sm">
            <span className="text-gray-500">Subtotal</span>
            <span className="font-medium text-gray-700">
              {formatCurrency(order.subtotal)}
            </span>
          </div>
          <div className="flex items-center justify-between text-sm">
            <span className="text-gray-500">Tax</span>
            <span className="font-medium text-gray-700">
              {formatCurrency(order.tax)}
            </span>
          </div>
          <div className="border-t border-gray-100 pt-3">
            <div className="flex items-center justify-between">
              <span className="text-base font-semibold text-gray-800">Total</span>
              <span className="text-xl font-bold text-gray-800">
                {formatCurrency(order.total)}
              </span>
            </div>
          </div>
        </div>
      </div>

      {/* Bill Section */}
      {canShowBillActions && (
        <div className="rounded-xl bg-white p-6 shadow-sm">
          <h2 className="mb-4 text-lg font-semibold text-gray-800">
            Billing
          </h2>

          {!bill ? (
            /* Request Bill / Call Waiter Buttons */
            <div className="flex flex-col gap-3 sm:flex-row">
              <button
                onClick={() => requestBillWithOptions()}
                disabled={generatingBill}
                className="flex flex-1 items-center justify-center gap-2 rounded-xl bg-[#c47a5a] px-6 py-3 font-semibold text-white transition-colors hover:bg-[#b56a4a] disabled:cursor-not-allowed disabled:opacity-50"
              >
                {generatingBill ? (
                  <span className="h-5 w-5 animate-spin rounded-full border-2 border-white border-t-transparent" />
                ) : (
                  <FiFileText className="h-5 w-5" />
                )}
                {generatingBill ? "Requesting..." : "Request Bill"}
              </button>
              <button
                onClick={() => requestBillWithOptions({ callWaiter: true })}
                disabled={generatingBill}
                className="flex flex-1 items-center justify-center gap-2 rounded-xl border-2 border-[#c47a5a] px-6 py-3 font-semibold text-[#c47a5a] transition-colors hover:bg-[#c47a5a]/5 disabled:cursor-not-allowed disabled:opacity-50"
              >
                <FiClock className="h-5 w-5" />
                Call Waiter for Bill
              </button>
            </div>
          ) : (bill as any).status === "requested" ? (
            /* Bill Requested - Show bill details and payment options */
            <div className="space-y-4">
              <div className="flex items-center gap-2 rounded-lg bg-yellow-50 border border-yellow-200 p-3">
                <FiClock className="h-5 w-5 text-yellow-600" />
                <span className="font-semibold text-yellow-700">Waiting for staff confirmation...</span>
              </div>

              {/* Bill Info */}
              <div className="rounded-lg bg-gray-50 p-4 space-y-2">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Bill Number</span>
                  <span className="font-medium text-gray-700">
                    {bill.billNumber}
                  </span>
                </div>
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Amount</span>
                  <span className="font-semibold text-gray-800">
                    {formatCurrency(bill.total)}
                  </span>
                </div>
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Payment Method</span>
                  <span className="font-medium capitalize text-gray-700">
                    {bill.paymentMethod}
                  </span>
                </div>
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Payment Status</span>
                  <span className="inline-flex items-center gap-1 text-sm font-medium text-gray-500">
                    <FiXCircle className="h-3.5 w-3.5" />
                    Unpaid
                  </span>
                </div>
              </div>

              {/* Download PDF */}
              <button
                onClick={downloadPdf}
                className="flex w-full items-center justify-center gap-2 rounded-xl border border-[#c47a5a] px-6 py-3 font-semibold text-[#c47a5a] transition-colors hover:bg-[#c47a5a]/5"
              >
                <FiDownload className="h-5 w-5" />
                Download PDF
              </button>

              {/* Pay Bill */}
              <div className="space-y-3 border-t border-gray-100 pt-4">
                <h3 className="text-sm font-semibold text-gray-800">
                  Pay Bill
                </h3>

                {/* Payment Method Selection */}
                <div className="flex gap-2">
                  {PAYMENT_METHODS.map((method) => (
                    <button
                      key={method.value}
                      onClick={() => setSelectedPayment(method.value)}
                      className={`flex-1 rounded-lg border px-3 py-2 text-sm font-medium transition-colors ${
                        selectedPayment === method.value
                          ? "border-[#c47a5a] bg-[#c47a5a]/5 text-[#c47a5a]"
                          : "border-gray-200 text-gray-600 hover:bg-gray-50"
                      }`}
                    >
                      {method.label}
                    </button>
                  ))}
                </div>

                <button
                  onClick={payBill}
                  disabled={payingBill}
                  className="flex w-full items-center justify-center gap-2 rounded-xl bg-[#c47a5a] px-6 py-3 font-semibold text-white transition-colors hover:bg-[#b56a4a] disabled:cursor-not-allowed disabled:opacity-50"
                >
                  {payingBill ? (
                    <span className="h-5 w-5 animate-spin rounded-full border-2 border-white border-t-transparent" />
                  ) : (
                    <FiCreditCard className="h-5 w-5" />
                  )}
                  {payingBill ? "Processing Payment..." : "Pay Bill"}
                </button>
              </div>
            </div>
          ) : (
            <div className="space-y-4">
              {/* Paid Badge */}
              {bill.isPaid && (
                <div className="flex items-center justify-center gap-2 rounded-lg bg-green-50 border border-green-200 p-3">
                  <FiCheckCircle className="h-5 w-5 text-green-600" />
                  <span className="font-semibold text-green-700">Paid</span>
                  {bill.paymentMethod && (
                    <span className="text-sm text-green-600">
                      via <span className="capitalize">{bill.paymentMethod}</span>
                    </span>
                  )}
                </div>
              )}

              {/* Bill Info */}
              <div className="rounded-lg bg-gray-50 p-4 space-y-2">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Bill Number</span>
                  <span className="font-medium text-gray-700">
                    {bill.billNumber}
                  </span>
                </div>
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Amount</span>
                  <span className="font-semibold text-gray-800">
                    {formatCurrency(bill.total)}
                  </span>
                </div>
                {bill.paymentMethod && (
                  <div className="flex items-center justify-between text-sm">
                    <span className="text-gray-500">Payment Method</span>
                    <span className="font-medium capitalize text-gray-700">
                      {bill.paymentMethod}
                    </span>
                  </div>
                )}
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-500">Payment Status</span>
                  <span
                    className={`inline-flex items-center gap-1 text-sm font-medium ${
                      bill.isPaid ? "text-green-600" : "text-gray-500"
                    }`}
                  >
                    {bill.isPaid ? (
                      <FiCheckCircle className="h-3.5 w-3.5" />
                    ) : (
                      <FiXCircle className="h-3.5 w-3.5" />
                    )}
                    {bill.isPaid ? "Paid" : "Unpaid"}
                  </span>
                </div>
                {bill.isPaid && bill.paidAt && (
                  <div className="flex items-center justify-between text-sm">
                    <span className="text-gray-500">Paid At</span>
                    <span className="font-medium text-gray-700">
                      {formatDate(bill.paidAt)}
                    </span>
                  </div>
                )}
              </div>

              {/* Download PDF */}
              <button
                onClick={downloadPdf}
                className="flex w-full items-center justify-center gap-2 rounded-xl border border-[#c47a5a] px-6 py-3 font-semibold text-[#c47a5a] transition-colors hover:bg-[#c47a5a]/5"
              >
                <FiDownload className="h-5 w-5" />
                Download PDF
              </button>

              {/* Pay Bill */}
              {!bill.isPaid && (
                <div className="space-y-3 border-t border-gray-100 pt-4">
                  <h3 className="text-sm font-semibold text-gray-800">
                    Pay Bill
                  </h3>

                  {/* Payment Method Selection */}
                  <div className="flex gap-2">
                    {PAYMENT_METHODS.map((method) => (
                      <button
                        key={method.value}
                        onClick={() => setSelectedPayment(method.value)}
                        className={`flex-1 rounded-lg border px-3 py-2 text-sm font-medium transition-colors ${
                          selectedPayment === method.value
                            ? "border-[#c47a5a] bg-[#c47a5a]/5 text-[#c47a5a]"
                            : "border-gray-200 text-gray-600 hover:bg-gray-50"
                        }`}
                      >
                        {method.label}
                      </button>
                    ))}
                  </div>

                  <button
                    onClick={payBill}
                    disabled={payingBill}
                    className="flex w-full items-center justify-center gap-2 rounded-xl bg-[#c47a5a] px-6 py-3 font-semibold text-white transition-colors hover:bg-[#b56a4a] disabled:cursor-not-allowed disabled:opacity-50"
                  >
                    {payingBill ? (
                      <span className="h-5 w-5 animate-spin rounded-full border-2 border-white border-t-transparent" />
                    ) : (
                      <FiCreditCard className="h-5 w-5" />
                    )}
                    {payingBill ? "Processing Payment..." : "Pay Bill"}
                  </button>
                </div>
              )}
            </div>
          )}
        </div>
      )}
    </div>
  );
}
